* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  text-align: center;
  background-color: rgba(26, 68, 255, .8);
  padding: 20px;
}

.search-form > input {
  text-align: center;
  height: 30px;
  background-color: rgba(248, 248, 126, .54);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline-color: #ff0;
  margin-right: 20px;
}

.search-form > button {
  height: 30px;
  background-color: rgba(248, 248, 126, .54);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 9px;
  padding: 5px;
  font-family: inherit;
}

.search-form > button:hover {
  outline: 2px solid #ff0;
}

.gallery {
  width: 100%;
  max-width: 1440px;
  flex-wrap: wrap;
  gap: 20px;
  padding: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
}

.img-photo {
  width: 100%;
  height: 208px;
  display: block;
}

.info {
  justify-content: center;
  gap: 8px;
  list-style: none;
  display: flex;
}

.info-item {
  text-align: center;
}

.card-description {
  border: 1px solid rgba(161, 153, 153, .8);
  padding: 15px;
}

.load-morebtn {
  height: 30px;
  background-color: rgba(248, 248, 126, .54);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 9px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  font-family: inherit;
  display: block;
}

.load-morebtn:hover {
  background-color: #ff0;
}

